import classes from './FeaturesToolsCard.module.css';
import {Card,Col} from 'react-bootstrap';

const FeatureToolsCard = (props) => {
    return (
        <Col lg={3} md={6} sm={12}>
        <Card className={classes.card_item} style={{ width: '259px',height: '255px',margin: '0.8rem' }}>
            <Card.Body className={classes.card_body}>
                <img src={require("./"+props.imgsrc+".png").default} alt="img"></img>
                <Card.Title className={classes.title_item} style={{padding: "0.5rem 0rem"}}>{props.title}</Card.Title>
                <Card.Text className={classes.desc_item}>
                    {props.desc}
                </Card.Text>
            </Card.Body>
        </Card>
        </Col>
    );
}

export default FeatureToolsCard;