import {Card,Col} from 'react-bootstrap';
import classes from './OurProjectCard.module.css';

const OurProjectCard = (props) => {
    return (
        <Col sm={12} md={6} lg={4}>
        <Card className={classes.card_item} style={{ width: '15rem',backGroundColor: "#F6F5FB" }}>
            <Card.Img variant="top" src={props.image} />
            <Card.Body className={classes.card_body}>
                <Card.Title className={classes.card_title} style={{textAlign: "center"}}>{props.title}</Card.Title>
            </Card.Body>
        </Card>
        </Col>
    );
}

export default OurProjectCard;