import React from 'react'
import {Navbar} from 'react-bootstrap'
import { Container } from 'react-bootstrap'
import Nav from 'react-bootstrap/Nav'
import logo from '../images/logo.png'
import {Link} from 'react-router-dom';
import './NavbarComp.css';

function NavbarComp() {
    return (
        <div className="navig">
                <Container fluid className="Navigation" style={{backgroundColor:'#FDFAFE'}}>
                    <Navbar className="d-flex justify-content-between" expand="sm">
                      <div>  
                        <Navbar.Brand href="" >
                            <img src={logo} alt="logo" className="ms-4"/>
                        </Navbar.Brand>
                      </div>  
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                      <div>  
                        <Navbar.Collapse>
                        <Nav className="me-auto" style={{color:"#272343"}}>
                        <Link to="/" style={{marginRight: "1rem", textDecoration: "none",color:"#272343",fontWeight:"600"}}>Home</Link> &nbsp;&nbsp;
                        <Link to="/aboutus" style={{marginRight: "1rem", textDecoration: "none",color:"#272343",fontWeight:"600"}}>About Us</Link> &nbsp;&nbsp;
                        <Link to="/services" style={{marginRight: "1rem", textDecoration: "none",color:"#272343",fontWeight:"600"}}>Services</Link> &nbsp;&nbsp;
                        <Link to="/pricing" style={{marginRight: "1rem", textDecoration: "none",color:"#272343",fontWeight:"600"}}>Pricing</Link> &nbsp;&nbsp;
                        <Link to="/contact" style={{marginRight: "1rem", textDecoration: "none",color:"#272343",fontWeight:"600"}}>Contact</Link> &nbsp;&nbsp;
                        </Nav>
                        </Navbar.Collapse>
                      </div>
                    </Navbar>
                </Container>
        </div>
    )
}

export default NavbarComp
