import { Container } from 'react-bootstrap';
import classes1 from './AboutUsSectionOne.module.css';
import classes from './TeamSection.module.css';
import {Row} from 'react-bootstrap';
import TeamItem from './TeamItem';
import '../../Home/components/SectionHP1css.css';

const TeamSection = () => {
    return (
        <div className={classes.team_section+" section2"}>
            <Container>
            <div className={classes.header + ' ' + classes1.about_one}>
                    <span>
                    TEAM
                    </span>
                    <h3>
                    Meet Our Team
                    </h3>
                    <div>
                        <div className={classes1.line_one}> </div>
                        <div className={classes1.line_two}> </div>
                    </div>
                </div>
                <div className={classes.card_container}>
                    <Row>
                        <TeamItem url="https://ajeyainfosol.com/assets/images/aboutus/jagansir.jpg" name="Jaganathan T" role="Managing Director"/>
                        <TeamItem url="https://ajeyainfosol.com/assets/images/aboutus/ajpradeep.png" name="Pradeep" role="Product Manager"/>
                        <TeamItem url="https://ajeyainfosol.com/assets/images/aboutus/ajsanthiya.png" name="Santhiya G" role="Senior Developer"/>
                        <TeamItem url="https://ajeyainfosol.com/assets/images/aboutus/ajveera.jpg" name="Veera" role="Senior Software Engineer"/>
                        <TeamItem url="https://ajeyainfosol.com/assets/images/aboutus/ajmani.png" name="Manikandan B" role="UI/UX Developer"/>
                        <TeamItem url="https://ajeyainfosol.com/assets/images/aboutus/ajrohith.jpg" name="Rohith R" role="Junior Software Developer"/>
                    </Row>
                </div>
            </Container>
        </div>
    );
}

export default TeamSection;