import classes from './NewsLetter.module.css';
import { Container,Row,Col } from 'react-bootstrap';

const NewsLetter = (props) => {
    return (
        <div className={classes.jumbotron}>
            <div className={classes.background}>
                <Container className={classes.center_box}>
                    <div className={classes.content}><p>Get Started Instantly!</p></div>
                    <h4>Get the only new update from this<br /> newsletter</h4>
                    <br />
                    <form className={classes.form_item} style={{marginRight: "1rem"}}>
                        <Row>
                        <Col xs={6} sm={6} md={6} lg={6}><input type="email" placeholder="Enter your email" name="EMAIL" required="" /></Col> 
                        <Col xs={6} sm={6} md={6} lg={6}><button type="submit">Subscribe</button></Col>
                        </Row>
                    </form>
                </Container>
            </div>
        </div>
    );
}

export default NewsLetter;