import { Col,Card} from 'react-bootstrap';
import classes from './TeamItem.module.css';

// icons
import { FaLinkedinIn, FaFacebookF,FaTwitter } from "react-icons/fa"
import {IoLogoInstagram} from "react-icons/io"

const TeamItem = (props) => {
    return (
        <Col>
            <Card className={classes.card_item} style={{ width: '18rem' }}>
                <Card.Img variant="top" src={props.url} />
                <Card.Body className={classes.card_body}>
                    <Card.Title className={classes.title}>{props.name}</Card.Title>
                    <Card.Text className={classes.content}>
                        {props.role}
                    </Card.Text>
                    <div className = {classes.icons_container}>
                        <div><FaLinkedinIn/></div>
                        <div><FaFacebookF/></div>
                        <div> <FaTwitter/></div>
                        <div><IoLogoInstagram/></div>
                
                    </div>
                </Card.Body>
            </Card>
        </Col>
    );
}

export default TeamItem;