import classes from './FeaturesTools.module.css';
import { Container,Row } from 'react-bootstrap';
import classes1 from './AboutUsSectionOne.module.css';
import FeaturesToolsCard from './FeaturesToolsCard';

const FeaturesTools = () => {
    return (
        <div>
            <Container>
                <div className={classes.header + ' ' + classes1.about_one}>
                    <span>
                        CARE FEATURES
                    </span>
                    <h3>
                        Create Awesome Service With Our Tools
                    </h3>
                    <div>
                        <div className={classes1.line_one}> </div>
                        <div className={classes1.line_two}> </div>
                    </div>
                </div>
                <div className={classes.card_section}>
                    <Row style={{display: "flex",alignItems: "center",justifyContent: "center"}}>
                    <FeaturesToolsCard title="Seo Consultancy" desc="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor" imgsrc="seo"/>
                    <FeaturesToolsCard title="Competitor Analysis" desc="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor" imgsrc="research"/>
                    <FeaturesToolsCard title="Social Media Marketing" desc="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor" imgsrc="laptop" />
                    <FeaturesToolsCard title="Skyrocketing Growth" desc="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor" imgsrc="analysis"/>
                    </Row>
                </div>
            </Container>
        </div>
    );
}

export default FeaturesTools;