import { Fragment } from "react";
import Jumbotron from "../AboutUs/components/Jumbotron";
import LogoSection from "../AboutUs/components/LogoSection";
import NewsLetter from "../AboutUs/components/NewsLetter";
import ServicesSection from "../AboutUs/components/ServicesSection";
import FooterSection from "../Home/components/FooterSection";
import NavbarComp from "../Home/components/NavbarComp";
import SectionHP5 from "../Home/components/SectionHP5";

const Services = () => {
    return (
        <Fragment>
            <NavbarComp />
            <Jumbotron heading="Services" pageName="Services" />
            <ServicesSection />
            <SectionHP5 />
            <NewsLetter />
            <LogoSection />
            <FooterSection />
        </Fragment>
    );
}

export default Services;