import classes1 from './AboutUsSectionOne.module.css';
import {Container,Row} from 'react-bootstrap';
import classes from './OurProjectSection.module.css';
import OurProjectCard from './OurProjectCard';

const OurProjectSection = () => {
    return (
        <div className={classes.our_project}>
            <Container>
                <div className={classes.header + ' ' + classes1.about_one}>
                    <span>
                        CARE FEATURES
                    </span>
                    <h3>
                        Create Awesome Service With Our Tools
                    </h3>
                    <div>
                        <div className={classes1.line_one}> </div>
                        <div className={classes1.line_two}> </div>
                    </div>
                </div>
                <div style={{margin: "3rem"}}>
                    <Row>
                    <OurProjectCard image="https://neemo-react.envytheme.com/images/project/1.jpg" title="Branding"/>
                    <OurProjectCard image="https://neemo-react.envytheme.com/images/project/2.jpg" title="Marketing"/>
                    <OurProjectCard image="https://neemo-react.envytheme.com/images/project/3.jpg" title="SEO"/>
                    <OurProjectCard image="https://neemo-react.envytheme.com/images/project/4.jpg" title="Web Design"/>
                    <OurProjectCard image="https://neemo-react.envytheme.com/images/project/5.jpg" title="Web Development"/>
                    <OurProjectCard image="https://neemo-react.envytheme.com/images/project/6.jpg" title="Digital marketing"/>
                    </Row>
                </div>
            </Container>
        </div>
    );
}

export default OurProjectSection;