import classes from './ServicesSectionCard.module.css';
import {Card,Col} from 'react-bootstrap';

const ServicesSectionCard = (props) => {
    return (
        <Col sm={12} md={6} lg={4} style={{padding: "2rem"}}>
        <Card style={{ width: '18rem' }}  className={classes.card_item}>
            <Card.Body >
                <img src={require("./"+props.image+".png").default} alt="img"></img>
                <Card.Title ><a href="/" className={classes.title_item}>{props.title}</a></Card.Title>
                <Card.Text className={classes.text_item}>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incutu labore et dolore magna aliqua.
                </Card.Text>
            </Card.Body>
        </Card>
        </Col>
    );
}

export default ServicesSectionCard;