import React from 'react'
import './SectionHP1css.css'
import Col from 'react-bootstrap/Col' 
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Button from 'react-bootstrap/Button'
// import Image from 'react-bootstrap/Image'
import mygifonce from '../images/mygifoncetrans.gif'
import c from '../images/CircledPlay.png'

function SectionHP1() {
    return (
        <Container className = "section1" style={{backgroundColor:'#FDFAFE'}}>
            <Row className="">
                <Col className="py-5 d-flex flex-column" md="6">
                 <div className="mt-5 align-self-center"> 
                  <p className="h1 mt-5" style={{color:"#272343" ,marginTop:"200px"}}>
                   Digital Agency with Excellent Service
                  </p>
                  <p style={{color:"#A9A9A9",fontSize:"large",fontWeight:"400"}}>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt
                  </p>
                  <Button variant="primary" style={{backgroundColor:"#ff5f77",borderColor:"#ff5f77",marginRight :"10px",fontSize:"larger",fontWeight:"400",padding:"4"}}>More About Us</Button>
                  <Button variant="primary" style={{backgroundColor:"#fdfafe",borderColor:"#ff5f77",color:"#272343",fontSize:"larger",fontWeight:"400",padding:"4"}}><img src={c} alt="playbutton" style={{height:"20px",width:"20px",marginBottom:"2px"}}/> Watch Video</Button>               
                 </div> 
                </Col>

                <Col md="6">
                    <img src={mygifonce} alt="mygifonce" className="img-fluid"/>
                </Col>
            </Row>
        </Container>
    )
}

export default SectionHP1
