import classes from './ServicesSection.module.css';
import classes1 from './AboutUsSectionOne.module.css';
import {Container,Row} from 'react-bootstrap';
import ServicesSectionCard from './ServicesSectionCard';
import '../../Home/components/SectionHP1css.css';

const ServicesSection = () => {
    return (
        <div className={classes.services_section+ " section2"}>
            <Container>
                <div className={classes.header + ' ' + classes1.about_one}>
                    <span>
                        CARE FEATURESs
                    </span>
                    <h3>
                        Create Awesome Service With Our Tools
                    </h3>
                    <div>
                        <div className={classes1.line_one}> </div>
                        <div className={classes1.line_two}> </div>
                    </div>
                </div>
               <div>
                   <Row >
                   <ServicesSectionCard image="service2" title="Real Time And Data"/>
                   <ServicesSectionCard image="service3" title="Search Engine Optimization"/>
                   <ServicesSectionCard image="service1" title="Social Media Strategy"/>
                   <ServicesSectionCard image="service4" title="Online Media Management"/>
                   <ServicesSectionCard image="service5" title="Reporting & Analysis"/>
                   <ServicesSectionCard image="service6" title="Penalty Recovery"/>
                   </Row>
               </div>
            </Container>
        </div>
    );
}

export default ServicesSection;