import React from 'react'
import { Col, Container, Row, Button } from 'react-bootstrap'
import img from '../images/analysisimage.png'
import Form from 'react-bootstrap/Form'

function SectionHP10() {
    return (
        <Container className="py-5 my-5">
            <Row>
                <Col md={7} className = "d-flex flex-column">
                  <div className = "my-auto">  
                     <img className="img-fluid" src={img} alt="Image5"/>
                   </div>  
                </Col>

                <Col md={5}>
                    <div className="my-auto"> 
                      <p style={{color:"#ff5f77",fontWeight:"600"}}>ANALYSIS</p>
                      <p className="h1">Get Free SEO Analysis</p>
                      <p style={{color:"#A9A9A9",fontSize:"medium",fontWeight:"400",padding:"5px"}}>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                        sed do eiod tempor incididunt ut labore et dolore 
                        magna aliqua.
                      </p>

                      <Form.Floating className="mb-3">
                        <Form.Control
                        id="floatingInputCustom"
                        type="Name"
                        placeholder="Name"
                        />
                        <label htmlFor="floatingInputCustom">Your Name</label>
                      </Form.Floating>

                      <Form.Floating className="mb-3">
                        <Form.Control
                        id="floatingInputCustom"
                        type="Name"
                        placeholder="Name"
                        />
                        <label htmlFor="floatingInputCustom">Email</label>
                      </Form.Floating>

                      <Form.Floating className="mb-3">
                        <Form.Control
                        id="floatingInputCustom"
                        type="Name"
                        placeholder="Name"
                        />
                        <label htmlFor="floatingInputCustom">Phone</label>
                      </Form.Floating>

                      <Form.Floating className="mb-3">
                        <Form.Control
                        id="floatingInputCustom"
                        type="Name"
                        placeholder="Name"
                        />
                        <label htmlFor="floatingInputCustom">Website</label>
                      </Form.Floating>

                      <Button variant="primary" style={{backgroundColor:"#ff5f77",borderColor:"#ff5f77",marginRight :"10px",fontSize:"larger",fontWeight:"400",padding:"5"}}>Send Message</Button>  
                    </div> 
                </Col>
            </Row>
        </Container>
    )
}

export default SectionHP10
